.header {
  background-image: url("../img/header-bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;

  &__container {
    height: 100%;
    padding-top: 57px;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
  }
  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
  }
  &__content {
  }
  &__title {
    margin: 0;
    padding-bottom: 21px;
    font-size: 60px;
    line-height: 70px;
    letter-spacing: 5.70937px;
    color: var(--light-color);
  }
  &__text {
    margin: 0;
    font-size: 16px;
    line-height: 19px;
    color: var(--light-color);
  }
  &__down {
    margin: 0;
    font-size: 11px;
    line-height: 13px;
    letter-spacing: 3.7px;
    color: var(--light-color);
  }
}
